import React, { useState } from "react";
import { Container, Row, Col } from "react-bootstrap";
import { Link } from "gatsby";
import { StaticImage } from "gatsby-plugin-image";
import Faq from "../../components/common/faq";
import ContactButton from "../../components/common/connect-button";

import rocket from "../../styles/assets/images/stocktrading/rocket.svg";
import rocket_base from "../../styles/assets/images/stocktrading/rocket_base.svg";
import choose from "../../styles/assets/images/stocktrading/choose_image.webp";
import test from "../../styles/assets/images/stocktrading/process_four.svg";

import TalkToUs from "../../components/common/talk-to-us";
import { yupResolver } from "@hookform/resolvers/yup";
import * as Yup from "yup";
import Loading from "../../components/common/loading";
import { useForm } from "react-hook-form";
import { Country } from "country-state-city";
import { collection, addDoc } from "firebase/firestore/lite";
import { utils } from "../../core/helper";
import "animate.css";
import CtaButton from "../../components/common/contactbutton";
import MoveForward from "../../components/common/move-forward";
import StockTradingFeatures from "./StockTradingFeatures";
import AdvancedFeatures from "./AdvancedFeatures";
import ConnectButton from "../../components/common/connect-button";

const validationSchema = Yup.object().shape({
  name: Yup.string().label("Name").required(),
  email: Yup.string().label("Email").required(),
  country: Yup.string().label("Country").required(),
  mobile: Yup.string().label().required("Contact is a required field"),
  content: Yup.string().label("Content").required(),
});

const StockTradingService = (props) => {
  const { db } = props;
  const [loading, setLoading] = useState(false);
  const [talkToUsShow, setTalkToUsShow] = useState(false);
  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm({
    resolver: yupResolver(validationSchema),
  });

  const handleToggleTalkToUs = () => {
    setTalkToUsShow(!talkToUsShow);
    reset();
  };

  const countries = Country.getAllCountries();

  const handleOnSubmit = async (values) => {
    const { name, email, country, content, mobile } = values;
    const payload = {
      name,
      email,
      country,
      content,
      mobile,
      date: new Date(),
    };
    setLoading(true);
    try {
      const clients = collection(db, "clients");
      await addDoc(clients, payload);
      utils.showSuccssMsg("We will contact/talk to you soon");
      reset();
      setLoading(false);
      setTimeout(() => {
        handleToggleTalkToUs();
      }, 1000);
    } catch (e) {
      utils.showErrMsg("Please enter the valid details");
      setLoading(false);
    }
  };

  const [activeKey, setActiveKey] = useState(null);

  const toggleAccordion = (eventKey) => {
    setActiveKey(eventKey === activeKey ? null : eventKey);
  };

  const stockTradingFaqData = [
    {
      question: "How much does it cost to create a trading app?",
      answer:
        "The cost of creating a trading app varies based on features, complexity, and development time.",
    },
    {
      question: "What security measures protect my trading and investing app?",
      answer:
        "It includes data encryption, two-factor authentication, and multi-layered access controls.",
    },
    {
      question: "Is there an AI for stock trading?",
      answer:
        "Yes, AI is used in stock trading for automated decision-making through algorithms and robo-advisors.",
    },
    {
      question: "How to make an app like Zerodha?",
      answer:
        "To make a Zerodha-like app, hire a team, integrate real-time market data, and ensure regulatory compliance.",
    },
  ];

  return (
    <div className="stock_trading_wrapper">
      <section className="stock_trading_hero_sec">
        <div className="stock_hero_sec_lhs">
          <div className="stock_hero_title">
            <h1>Stock Trading App Development</h1>
          </div>
          <div className="stock_hero_description">
            <p>
              Transform your trading and investment experience with our skilled
              trading app developers, delivering customized solutions for your
              specific needs.
            </p>
          </div>

          <div className="decenter_hero_sec_btn">
            <Link to="/contact-us" className="demo_btn">
              View Live Demo
            </Link>
            <button
              type="submit"
              className="consult_btn"
              onClick={handleToggleTalkToUs}
            >
              Free Consult
            </button>
          </div>
        </div>
        <div className="stock_hero_sec_rhs">
          <StaticImage
            src="../../styles/assets/images/stocktrading/stock_trading.webp"
            alt="stock trading"
          />
        </div>
      </section>{" "}
      <section className="modern_way_section stock_trading_custom_padding">
        <div className="modern_way_sec_lhs">
          <h2>Stock Trading App: The Modern Way of Trading</h2>
          <p>
            Trading, integral to human civilization since agrarian times,
            featured diverse methods among isolated communities. Barter systems
            prevailed, yet a lack of a universal measure posed challenges. Money
            emerged as the solution, establishing a standard for product
            valuation and propelling economic and financial progress, ushering
            in the era of stock trading.
          </p>
          <p>
            Harness the power of high-speed internet and growing financial
            literacy by investing in stock trading apps. With mobile devices and
            internet connectivity as the only prerequisites, Justtry can turn
            your vision into a successful reality. Partner with us today!
          </p>
          <ContactButton justifyContentPosition="connect_start" />
        </div>
        <div className="modern_way_sec_rhs">
          <StaticImage
            className="modern_way_image_wrapper"
            src="../../styles/assets/images/stocktrading/student.webp"
            alt="stocktrading student"
          />
        </div>
      </section>
      <section className="launch_app_section stock_trading_custom_padding ">
        <div className="stock_trading_launch_wrapper">
          <div className="stock_trading_launch_lhs">
            <img
              className="first_launch"
              src={rocket_base}
              alt="app_launch_image"
            />
            <img
              className="second_launch"
              src={rocket}
              alt="app_launch_image"
            />
          </div>
          <div className="stock_trading_launch_rhs">
            <h3>
              Be the trailblazer of the digital era. Launch your custom stock
              trading app today!
            </h3>
            <div className="launch_btn_wrapper">
              <button
                type="submit"
                className="launch_btn"
                onClick={handleToggleTalkToUs}
              >
                Talk to us
              </button>
            </div>
          </div>
        </div>
      </section>
      <section className="stock_trading_app_works_section stock_trading_custom_padding">
        <div className="stock_trading_app_wrapper">
          <div className="stock_trading_app_tile">
            <h2>Decoding the Magic: How a Stock Trading App Works</h2>
          </div>
          <div className="stock__trading_app_content">
            <div className="stock_trading_app_lhs">
              <p>
                In essence, a stock trading app serves as the gateway for
                individuals to participate in the dynamic world of stock
                markets. Users simply download the app onto their smartphones to
                embark on a journey of investing in stocks, whether it's for the
                long haul or short-term gains. Beyond just investing, these apps
                provide real-time market tracking capabilities, keeping users
                updated on stock prices and market trends directly from the palm
                of their hand.
              </p>
            </div>
            <div className="stock_trading_app_center">
              <StaticImage
                src="../../styles/assets/images/stocktrading/teaching.webp"
                alt="stocktrading student"
              />
            </div>
            <div className="stock_trading_app_rhs">
              <p>
                Stock brokers are pivotal on online trading platforms, serving
                as crucial intermediaries for smooth transactions and
                commission-based revenue. Explore financial opportunities with a
                leading stock trading app, prioritizing brokers. Dive into smart
                investments and strategic trading via user-friendly interfaces.
                Utilize live market insights to capitalize on emerging trends.
                Elevate your trading with efficient, innovative platforms,
                confidently navigating the financial landscape with precision.
              </p>
            </div>
          </div>
        </div>
      </section>
      <Row>
        <Col>
          <div className="crypto_btns_grp">
            <Link className="prime_submit_btn" to="/contact-us">
              View Live Demo
            </Link>
            <button
              className="second_consult_btn"
              type="button"
              onClick={handleToggleTalkToUs}
            >
              Free Consult
            </button>
          </div>
        </Col>
      </Row>
      <section className="stock_trading_process_section stock_trading_custom_padding">
        <div className="stock_trading_process_header">
          <h2>
            Systematic Blueprint of Our Stock Trading App <br />
            Development Process
          </h2>
        </div>
        <div className="stock_trading_process">
          <div className="stock_trading_dev_process">
            <h2>01</h2>
          </div>
          <div className="stock_trading_dev_process">
            {utils.stockIdResearchIcon(280, 196)}
          </div>
          <div className="stock_trading_dev_process">
            <h4>Ideation and Research</h4>
            <p>
              Before embarking on any project, thorough planning is paramount.
              This phase involves defining the requirements, features, and
              target audience for your stock trading application. In addition,
              our expert consultants collaborate with you to finalize a
              marketing strategy and monetization model to ensure your trading
              app's profitability.
            </p>
          </div>
        </div>
        <div className="stock_trading_process">
          <div className="stock_trading_develop_process">
            <h4>Design</h4>{" "}
            <p>
              Our seasoned team of UX designers transforms your innovative
              concepts into visual representations using graphical designs. If
              required, they can also create dynamic prototypes for testing or
              proof of concept, providing insights into your app's market
              potential before launch.
            </p>
          </div>
          <div className="stock_trading_develop_process">
            {utils.stockDesignIcon(280, 186)}
          </div>
          <div className="stock_trading_develop_process">
            <h2>02</h2>
          </div>
        </div>
        <div className="stock_trading_process">
          <div className="stock_trading_dev_process">
            <h2>03</h2>
          </div>
          <div className="stock_trading_dev_process">
            {utils.stockDevIcon(280, 280)}
          </div>
          <div className="stock_trading_dev_process">
            <h4>Development</h4>
            <p>
              This pivotal stage involves converting designs into code. Our
              developers, well-versed in your project's requirements, utilize
              the perfect technology stack for front-end and back-end
              development and initiate coding. They also advise on integrating
              third-party APIs to enhance performance and cybersecurity.
            </p>
          </div>
        </div>
        <div className="stock_trading_process">
          <div className="stock_trading_develop_process">
            <h4>Testing</h4>
            <p>
              Quality assurance is paramount. Our QA testers conduct
              comprehensive end-to-end testing to eliminate bugs and fortify
              your stock trading app against DDoS attacks, ensuring a smooth
              user experience.
            </p>
          </div>
          <div className="stock_trading_develop_process">
            <img
              width={280}
              height={196.3}
              src={test}
              alt="trade app process explanations"
            />
          </div>
          <div className="stock_trading_develop_process">
            <h2>04</h2>
          </div>
        </div>
        <div className="stock_trading_process">
          <div className="stock_trading_dev_process">
            <h2>05</h2>
          </div>
          <div className="stock_trading_dev_process">
            {utils.stockDeployIcon(280, 196)}
          </div>
          <div className="stock_trading_dev_process">
            <h4>Deployment and Maintenance</h4>
            <p>
              With a fully functional stock trading app, it's time to launch and
              market it. We create targeted marketing campaigns to communicate
              your app's unique value proposition to your audience. After
              generating interest, we assist in deploying your app on cloud
              servers or locally-hosted systems. Post-launch, we actively gather
              user feedback and provide ongoing updates and maintenance to meet
              your evolving needs.
            </p>
          </div>
        </div>
      </section>
      <section className="stock_trading_feature_section stock_trading_custom_padding">
        <div className="stock_trading_feature_top">
          <h2>Key Features of Our Stock Trading Application</h2>
        </div>
        <Row className="stock_trading_feature_bottom">
          <Col md={6}>
            <div className="stock_trading_feature_img_wrapper">
              <StaticImage
                src="../../styles/assets/images/stocktrading/key_image.webp"
                alt="stocktrading student"
              />
              <ConnectButton justifyContentPosition="connect_center" />
            </div>
          </Col>

          <Col md={6}>
            <StockTradingFeatures />
          </Col>
        </Row>
      </section>
      <AdvancedFeatures />
      <CtaButton db={db} />
      <section className="stock_trading_choose_justtry stock_trading_custom_padding">
        <div className="stock_trading_choose_lhs">
          <h2>
            Why Justtry is Your Ultimate Choice for Stock Trading App
            Development
          </h2>
          <p>
            At Justtry, Your Satisfaction is Our Priority. Our team of seasoned
            professionals specializes in stock trading app development, equipped
            with a deep understanding of the intricacies of financial markets to
            ensure your app meets industry standards. We remain at the forefront
            of technology trends, infusing your app with cutting-edge features
            to keep it competitive and responsive to market shifts. Our
            unwavering commitment extends well beyond the development phase. We
            offer continuous support, addressing issues, rolling out updates,
            and fine-tuning performance to ensure your app's enduring success.
            Leverage our al-inclusive stock trading app development services
            today!
          </p>
          <ContactButton justifyContentPosition="connect_start" />
        </div>
        <div className="stock_trading_choose_rhs">
          <img src={choose} alt="girl_ui_image_indicates choose justtry" />
          <div className="stock_trading_ellipse"></div>
        </div>
      </section>
      <Faq faqData={stockTradingFaqData} />
      <Container>
        <MoveForward db={db} />
      </Container>
      <TalkToUs
        show={talkToUsShow}
        onHide={handleToggleTalkToUs}
        closeButton={true}
        size={"lg"}
      >
        <div className="screen__content">
          <form onSubmit={handleSubmit(handleOnSubmit)} className="login">
            <div className="login__field">
              <i className="login__icon fas fa-user"></i>
              <input
                type="text"
                className="login__input"
                {...register("name")}
                placeholder="Your Name"
              />
              <p className="text-danger mb-0">{errors.name?.message}</p>
            </div>
            <div className="login__field">
              <i className="login__icon fas fa-lock"></i>
              <input
                type="email"
                className="login__input"
                {...register("email")}
                placeholder="Email"
              />
              <p className="text-danger mb-0">{errors.email?.message}</p>
            </div>
            <div className="login__field">
              <i className="login__icon fas fa-lock"></i>
              <input
                type="text"
                className="login__input"
                placeholder="Skype / Mobile"
                {...register("mobile")}
              />
              <p className="text-danger mb-0">{errors.mobile?.message}</p>
            </div>
            <div className="login__field">
              <i className="login__icon fas fa-lock"></i>
              <select className="login__input" {...register("country")}>
                <option value="">Select</option>
                {countries.map((ele, ind) => {
                  return (
                    <option key={ind} value={ele.isoCode}>
                      {ele.name}
                    </option>
                  );
                })}
              </select>
              <p className="text-danger mb-0">{errors.country?.message}</p>
            </div>
            <div className="login__field">
              <i className="login__icon fas fa-lock"></i>
              <textarea
                id="content"
                placeholder="Type content here"
                rows={5}
                className="login__input"
                {...register("content")}
                maxLength={200}
              ></textarea>
              <p className="text-danger mb-0">{errors.content?.message}</p>
            </div>
            <button
              type="submit"
              disabled={loading}
              className="button login__submit"
            >
              <span className="button__text">
                {" "}
                Submit {loading && <Loading />}
              </span>
              <i className="button__icon fas fa-chevron-right"></i>
            </button>
          </form>
        </div>
        <div className="screen__background">
          <span className="screen__background__shape screen__background__shape4"></span>
          <span className="screen__background__shape screen__background__shape3"></span>
          <span className="screen__background__shape screen__background__shape2"></span>
          <span className="screen__background__shape screen__background__shape1"></span>
        </div>
      </TalkToUs>
    </div>
  );
};

export default StockTradingService;
